/*
    Author: Joren Thijs
*/
body {
  font-family: 'Muli', sans-serif;
  font-weight: 300;
  padding: 2.2em 3.6em; }

.navigation {
  position: relative;
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  align-items: center;
  margin-left: -1.3em;
  margin-right: -1.2em;
  margin-bottom: 2.5em; }
  .navigation .hamburger {
    cursor: pointer; }
  .navigation .github-mark {
    min-width: 35px;
    cursor: pointer;
    filter: invert(42%) sepia(70%) saturate(5682%) hue-rotate(213deg) brightness(100%) contrast(103%); }

.flex-spacer {
  flex: 1; }

h1, h2 {
  margin: 0.9em auto;
  max-width: 520px; }

.main-title {
  color: #424242;
  font-size: 1.6em;
  line-height: 1.5em; }

.sub-title {
  color: #6D6A6A;
  font-size: 1.3em; }

.introduction {
  color: #6D6A6A;
  font-size: 1em;
  margin-bottom: 2.5em; }

.learn-more {
  color: #FD2DEE;
  cursor: pointer;
  white-space: nowrap; }

.primary-action-large {
  color: #FFFFFF;
  font-size: 1.8em;
  text-align: center;
  margin: auto;
  max-width: 520px;
  line-height: 1.9em;
  border-radius: 25px;
  background: #1F6AFF;
  background: linear-gradient(125deg, #1F6AFF 0%, #FD2DEE 100%);
  margin-bottom: .5em; }

.secundairy-action-large-border {
  border-radius: 25px;
  background: #1F6AFF;
  background: linear-gradient(125deg, #1F6AFF 0%, #FD2DEE 100%);
  margin: auto;
  max-width: 520px;
  padding: 2px;
  margin-bottom: 2.5em; }
  .secundairy-action-large-border .secundairy-action-large {
    color: #1F6AFF;
    font-size: 1.8em;
    text-align: center;
    line-height: 1.9em;
    border-radius: 23px;
    background: white; }

.hero-image-container {
  position: relative;
  max-width: 520px;
  margin: auto; }
  .hero-image-container .hero-image {
    width: 100%; }
  .hero-image-container .hero-image-ref {
    font-size: .8em;
    text-decoration: none;
    color: #7e7e7e;
    position: absolute;
    bottom: -1.5em;
    right: 0; }
